.hero {
	position: relative;
	padding-top: 64px;
	&:before,
	&:after {
		content: "";
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
		min-height: 100vmax;
		background: $charcoal-grey;
		transform: skewY(-27deg);
		transform-origin: 100% 0;
		@include rmin($sm-to-md) {
			transform: skewY(-30deg); }
		@include rmin($md) {
			transform: skewY(-27deg); } }
	&:before {
		bottom: 50%;
		transform-origin: 100% 0;
		@include rmin($lg) {
			display: none; } }
	&:after {
		bottom: 1%;
		@include rmin($xs) {
			bottom: 3%; }
		@include rmin($sm) {
			bottom: 8%; }
		@include rmin($sm-to-md) {
			bottom: 26%; }
		@include rmin($md) {
			bottom: 30%; }
		@include rmin($lg) {
			bottom: 45%; }
		@include rmin($lg-to-xl) {
			bottom: 50%; } }
	&__in {
		position: relative; }
	&__lead {
		position: relative;
		text-align: center;
		font-size: 30px;
		color: $pale-grey;
		letter-spacing: -0.7px;
		line-height: 1.3;
		@include rmin($xs) {
			padding: 0 20px; }
		@include rmin($sm) {
			max-width: 385px;
			margin: 0 auto;
			padding: 0;
			font-size: 36px; }
		@include rmin($sm-to-md) {
			max-width: 508px;
			margin: 0 0 0	36px;
			text-align: left;
			font-size: 48px;
			font-weight: 300;
			line-height: 1.33;
			letter-spacing: -0.6px; }
		@include rmin($md) {
			margin-left: 44px; }
		@include rmin($lg) {
			margin-left: 166px; } }
	&__call {
		margin-top: 26px;
		@include rmin($sm-to-md) {
			margin: 32px 0 0 -7px; } } }


.megacoin {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	margin-bottom: -90px;
	padding: 66px 0 164px;
	overflow: hidden;
	@include rmin($xs) {
		margin-bottom: -25px;
		padding-bottom: 95px; }
	@include rmin($sm-to-md) {
		margin-bottom: -258px;
		padding: 40px 0 0;
		justify-content: center; }
	@include rmin($lg-to-xl) {
		margin-bottom: -230px;
		padding-top: 38px; }
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: -15%;
		width: 130%;
		height: 100%;
		background-image: url(../img/megacoin-sm-bg.svg);
		background-position: center bottom;
		background-repeat: no-repeat;
		background-size: cover;
		@include rmin($sm) {
			left: -25%;
			width: 150%; }
		@include rmin($sm-to-md) {
			display: none; } }
	&__img {
		display: none;
		@include rmin($sm-to-md) {
			display: block; }
		@include rmin($lg-to-xl) {
			width: 100%;
			max-width: 1018px;
			margin-left: 40px; } } }


.coin {
	flex-basis: 100%;
	text-align: center;
	margin-bottom: 53px;
	@include rmin($xs) {
		flex-basis: 50%; }
	@include rmin($sm) {
		flex-basis: 33%; }
	@include rmin($sm-to-md) {
		display: none; }
	&_secure {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/secure.svg); } }
	&_fast {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/fast.svg); } }
	&_cheap {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/cheap.svg); } }
	&_optimized {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/optimized.svg); } }
	&_versalite {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/versalite.svg); } }
	&_smart {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/smart.svg); } }
	&_efficient {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/efficient.svg); } }
	&_decentralized {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/decentralized.svg); } }
	&_borderless {
		.coin__pic {
			background-image: url(../multicolor-svg/coins/borderless.svg); } }
	&_mob-hidden {
		display: none;
		@include rmin($sm) {
			display: block; } }
	&__pic {
		position: relative;
		width: 122px;
		height: 122px;
		margin: 0 auto;
		background-size: contain;
		background-repeat: no-repeat; }
	&__text {
		font-size: 22px;
		color: $pale-grey;
		letter-spacing: 0.1px;
		text-shadow: -4px 0px 15px rgba(247, 249, 251, 0.5), 4px 0px 15px rgba(247, 249, 251, 0.5); } }
